@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,600;1,100;1,200;1,400;1,500&family=Quicksand:wght@300;400;500&display=swap');

*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
}

:root{
    --color-bg:#33457f;
    --color-bg-2:#283765;
    --color-1:#00dc00;
    --color-2:#26ff26;
    --color-3:#00c300;
    --color-4:#1cb81c;
    --color-5:#0eff0e;
    --color-white:#fff;
    --color-black:black;
    --color-light:rgba(255,255,255,0.6);
    --transition: all 400ms ease ;


    --container-width-lg:80%;
    --container-width-md:86%;
    --container-width-sm:90%;
}

html{
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg-2);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(./assats/bg-texture.png);
}
/*-------------------------------------general style------------------------------*/
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}
h1, h2, h3, h4, h5{
    font-weight: 500;
} 
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 7rem;
    height: 100vh;
}
section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}
section > h2{
    color: var(--color-4);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-2);
    transition: var(--transition);
}
a:hover{
    color: var(--color-white);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-2);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-4);
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-white);
    color: var(--color-4);
    border-color: transparent;
}
.btn-primary{
    background: var(--color-3);
    color: var(--color-white);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*-------------------------------medium----------------------------*/
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}


/*-------------------------------small----------------------------*/
@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }
    section{
        margin-top: 2rem;
    }
}